.recipe {
  @apply flex justify-center pt-28 px-4;
}
.recipe main {
  @apply rounded-t-lg overflow-hidden shadow-xl;
}
.recipe h1 {
  @apply mt-4 text-2xl px-3 text-gray-700 font-bold;
}
.recipe .description {
  @apply px-3 text-base leading-5 tracking-tight text-justify text-gray-600;
}
.recipe--image {
  @apply object-cover w-full h-64;
}
.recipe .meta {
  @apply my-3 px-3 text-gray-500 flex justify-between items-center;
}
.recipe .meta .user {
  @apply flex items-center;
}
.recipe .meta .user img {
  @apply w-12 h-12 mr-3 rounded-full;
}
.recipe .meta .user a {
  @apply underline font-medium;
}
.recipe .ingredients {
  @apply px-3;
}
.recipe .ingredients h3,
.recipe .instructions h3,
.recipe .comments h3 {
  @apply text-xl font-bold text-gray-700;
}
.recipe .ingredients h3 + p,
.recipe .instructions h3 + p,
.recipe .comments h3 + p {
  @apply text-xs mb-4 text-gray-500;
}
.recipe .instructions {
  @apply px-3 my-4;
}
.recipe .comments {
  @apply px-3 mb-8;
}
.recipe .comments h3 {
  @apply mb-4;
}
.recipe .comments h3 span {
  @apply font-light;
}
